<template>
  <div class="page login" v-loading="loading">
    <div class="input-box">
      <div class="login-title">登录</div>
      <div class="unit">
        <i class="icon el-icon-user"></i>
        <el-input v-model="username" placeholder="用户名" />
      </div>
      <div class="unit">
        <i class="icon el-icon-lock"></i>
        <el-input value v-model="password" placeholder="密码" show-password />
      </div>
      <div class="btns">
          <el-button type="info" round size="medium" @click="register">注册</el-button>
          <el-button type="primary" round size="medium" @click="submit">登录</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      password: "",
      loading: false
    };
  },
  methods: {
    submit() {
      if (!this.username) {
        this.$message({
          message: "请输入用户名！",
          type: "warning"
        });
        return false;
      }

      if (!this.password) {
        this.$message({
          message: "请输入密码！",
          type: "warning"
        });
        return false;
      }

      this.loading = true;

      const self = this;

      this.Bmob.User.login(this.username, this.password)
        .then(() => {
            // console.log(res)
            self.loading = false
            self.$message({
                message: '登录成功！',
                type: 'success',
                duration: 1000,
                onClose: () => {
                    self.$router.replace('/')
                }
            })
        }).catch(err => {
            // console.log(err)
            self.loading = false
            self.$message.error(`登录失败，请重试！提示：${err.error}`)
        });
    },
    register() {
        this.$router.push('/register')
    }
  }
};
</script>

