<template>
  <div id="app">
    <Nav title="开源说" :login="login" :username="username" />
    <div class="content">
      <Aside :show="asideShow" />
      <div class="wrap">
        <router-view />
      </div>
    </div>
  </div>
</template>

<script>
import Nav from "@/components/Nav.vue";
import Aside from "@/components/Aside.vue";

export default {
  components: {
    Nav,
    Aside
  },
  data() {
    return {
      asideShow: false,
      login: false,
      username: ""
    };
  },
  methods: {
    getStatus() {
      let current = this.Bmob.User.current();
      if (current === null) {
        if (["login", "register"].indexOf(this.$route.name) < 0) {
          this.$router.push("/login");
        }
      } else {
        this.login = true;
        this.username = current.username;
      }
    }
  },
  updated() {
    this.getStatus();
  },
  mounted() {
    this.getStatus();
  }
};
</script>


