<template>
  <div class="nav">
    <div class="meta">
      <div class="menu"></div>
      <div class="title">{{ title }}</div>
    </div>
    <div class="status">
      <div class="username" v-if="username">{{username}}</div>
      <div class="log act" @click="logout" v-if="username">退出登录</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {
      type: String
    },
    menu: {
      type: Boolean
    },
    username: {
      type: String
    }
  },
  mounted: function() {},
  methods: {
    logout() {
      const self = this;
      this.$confirm("当前用户退出登录, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        self.Bmob.User.logout();
        window.location.reload();
      });
    }
  }
};
</script>

