  flex: 1;
}
.title {
  font-size: 16px;
  color: #fff;
  font-weight: bold;
  padding-left: 20px;
}

.status {
  font-size: 0.8em;
  color: #fff;
  display: inline-flex;
  flex-wrap: nowrap;
}

.log {
  cursor: pointer;
  user-select: none;
  margin-left: 20px;
}
</style>
