<template>
  <div class="page register" v-loading="loading">
    <div class="input-box">
      <div class="login-title">注册</div>
      <div class="unit">
        <i class="icon el-icon-user"></i>
        <el-input v-model="username" placeholder="用户名" />
      </div>
      <div class="unit">
        <i class="icon el-icon-lock"></i>
        <el-input value v-model="password" placeholder="密码" show-password />
      </div>
      <div class="unit">
        <i class="icon el-icon-lock"></i>
        <el-input value v-model="repassword" placeholder="重复密码" show-password />
      </div>
      <div class="unit">
        <i class="icon el-icon-receiving"></i>
        <el-input value v-model="email" placeholder="邮箱" />
      </div>
      <div class="btns">
        <el-button type="primary" round size="medium" @click="submit">注册</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      username: "",
      password: "",
      repassword: "",
      email: "",
      loading: false
    };
  },
  methods: {
    submit() {
      if (!this.username) {
        this.$message({
          message: "请填写用户名！",
          type: "warning"
        });
        return false;
      }

      if (!this.password) {
        this.$message({
          message: "请输入密码！",
          type: "warning"
        });
        return false;
      }

      if (!this.repassword) {
        this.$message({
          message: "请重复密码！",
          type: "warning"
        });
        return false;
      }

      if (this.password !== this.repassword) {
        this.$message({
          message: "密码不一致！",
          type: "warning"
        });
        return false;
      }

      if (!this.email) {
        this.$message({
          message: "请输入邮箱！",
          type: "warning"
        });
        return false;
      }

      if (
        !/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/.test(this.email)
      ) {
        this.$message({
          message: "邮箱格式不正确！",
          type: "warning"
        });
        return false;
      }

      this.loading = true;

      this.Bmob.User.register({
        username: this.username,
        password: this.password,
        email: this.email
      })
        .then(() => {
          this.Bmob.User.login(this.username, this.password)
            .then(() => {
              this.loading = false;
              this.$message({
                message: `注册成功！正在登录...`,
                type: "success",
                duration: 1000,
                onClose: () => {
                  this.$router.replace('/')
                }
              });
            })
            .catch(err => {
              this.$message({
                message: `登录失败！请重新登录。${err.error}`,
                type: "warning",
                duration: 1000,
                onClose: () => {
                  this.$router.replace('/login')
                }
              });
            });
        })
        .catch(err => {
          this.loading = false;
          this.$message({
            message: `注册失败！原因：${err.error}`,
            type: "warning"
          });
        });
    }
  }
};
</script>

